<template>
  <!-- <div class="hello">
  </button>
  <transition name="aaa">
    <p v-if="message == 1">hello</p>
  </transition>
  </div> -->
  <div class="inner">
    <ul>
      <li v-for="list in lists" v-bind:class="list.classname">
        <div class="title">{{list.title}}</div>
        <div class="content">{{list.content}}</div>
      </li>
    </ul>
    <div class="intro">一年前端工作经验，三年前端学习经验<br/>性格乐观，良好的人际关系和沟通能力<br/>自学能力强，对待问题能冷静解决</div>
    <img src="../assets/photo2.jpg">
    <div class="line"></div>
  </div>
</template>

<script>
export default {
  name: 'slide2',
  props: ['message'],
  data () {
    return {
      lists: [
        { title: '出生年月：', content:'1995年08月', classname:'li1'},
        { title: '联系电话：', content:'15220037627', classname:'li2'},
        { title: '联系邮箱：', content:'719457865@qq.com', classname:'li1'},
        { title: '所在地：', content:'广东广州', classname:'li2'},
        { title: '生源地：', content:'广东汕头', classname:'li1'},
        { title: '毕业院校：', content:'广东轻工职业技术学院', classname:'li2'},
        { title: '学历：', content:'大专', classname:'li1'},
        { title: '目前状态：', content:'离职', classname:'li2'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.inner{
  width: 1000px;
  margin: 0 auto;
  height: 100%;
  position: relative;
}
ul{
  position: absolute;
  left: 50%;
  margin-left: -360px;
  top: 60px;
}
ul,li{
  list-style-type: none;
}
li{
  width: 300px;
  height: 35px;
  font: 18px/35px "Microsoft YaHei";
}
li.li1{
  color: #FFF;
}
li.li2{
  color: #DDD;
}
li .title{
  width: 100px;
  height: 30px;
  text-align: right;
  float: left;
}
li .content{
  width: 190px;
  height: 30px;
  text-align: left;
  float: left;
  margin-left: 10px;
}
img{
  width: 200px;
  border: 5px solid #FFF;
  position: absolute;
  left: 50%;
  margin-left: 70px;
  top: 70px;
  border-radius: 5px;
  transform:rotate(7deg);
}
.intro{
  width: 320px;
  color: #FFF;
  font: 18px/30px "Microsoft YaHei";
  position: absolute;
  left: 50%;
  margin-left: -350px;
  top: 400px;
}
.line{
  width: 2px;
  height: 460px;
  background: #BBB;
  position: absolute;
  left: 50%;
  top: 50px;
}
</style>



