<template>
	<div class="pag">
		<!-- <div ref="liref" id="aaa">{{ message }}</div> -->
		<!-- <button v-on:click="changeindex()">增加 1</button> -->
		<div class="bg-line"></div>
		<ul>
			<li v-for="(index,item) in items" @click="liclick (item) " ref="liref" >
				<div class="round"></div>
				<div class="title1">{{ index.title }}</div>
				<div class="title" v-if="item == message">{{ index.title }}</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
  name: 'pagination',
  props: ['message'],
  data () {
    return {
    	items: [
	      { num: '0',title: "首页" },
	      { num: '1',title: "个人信息" },
	      { num: '2',title: "专业技能" },
	      { num: '3',title: "工作经历" },
	      { num: '4',title: "项目经验" }/*,
	      { num: '5',title: "兴趣爱好" }*/
	    ]
    }
  },
  methods: {
      /*changeindex: function () {
      	//alert(this.message);
      	var a = this.message + 1;
        this.$emit('receive', a);
      },*/
      liclick :function(index) {
		this.$emit('receive', index);
　　　}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pag{
	position: fixed;
	right: 0px;
	top: 0px;
	width: 100px;
	height: 100%;
	max-height: 800px;
}
.bg-line{
	width: 6px;
	height: 100%;
	background: #FFF;
	position: absolute;
	right: 42px;
	top: 0px;
}
ul,li{
	list-style-type: none;
}
ul{
	position: absolute;
	right: 30px;
	top: 50%;
	margin-top: -150px;
}
li{
	width: 120px;
	height: 30px;
	border-radius: 15px;
	cursor: pointer;
	margin-bottom: 20px;
	position: relative;
	overflow: hidden;
}
li .title{
	width: 120px;
	height: 30px;
	background: #FFF;
	color: #00835A;
	position: absolute;
	left: 0px;
	top: 0px;
	font:15px/30px "Microsoft YaHei";
	border-radius:15px;
	display: block;
	letter-spacing: 1px;
}
li .round{
	width: 30px;
	height: 30px;
	border-radius: 15px;
	position: absolute;
	top: 0px;
	right: 0px;
	background: #FFF;
}
li .title1{
	width: 120px;
	height: 30px;
	background: #FFF;
	color: #00B38A;
	position: absolute;
	left: 0px;
	top: 0px;
	font:15px/30px "Microsoft YaHei";
	border-radius:15px;
	display: none;
	letter-spacing: 1px;
}
li:hover .title1{
	display: block;
  animation-name: title-show;
  animation-duration:1s;
  animation-iteration-count:1;
  animation-fill-mode:forwards;
}
@keyframes title-show{
0%   {opacity: 0;}
100% {opacity: 1;}
}
</style>



