<template>
  <div class="inner">
    <div class="line"></div>
    <div class="now">NOW</div>
    <div class="box-a">
      <div class="line1"></div>
      <div class="time">2017.07 ~ <br/>2017.10</div>
      <transition name="rightanim">
        <div class="right-div" v-if="message == 3">
          <div class="tri"></div>
          <div class="content">
            <div class="company">太平洋网络有限公司(PCgroup)</div>
            <div class="intro">前端开发工程师</div>
            <div class="m">在太平洋网络有限公司广州总部担任前端开发工程师，按照公司前端技术规范，负责太平洋电脑网、汽车网、家居网、亲子网、时尚网五网(PC端/移动端)专题页面单页开发。与后端、设计、编辑、销售等需求方交流对接。</div>
          </div>
          <div class="trapezoid">
            <div class="white"></div>
            <img src="../assets/company1.png">
          </div>
        </div>
      </transition>
    </div>
    <div class="box-b">
      <div class="line1"></div>
      <div class="time">2017.02 ~ <br/>2017.06</div>
      <transition name="rightanim1">
        <div class="right-div" v-if="message == 3">
          <div class="tri"></div>
          <div class="content">
            <div class="company">深圳市来福儿网络科技有限公司</div>
            <div class="intro">H5前端工程师</div>
            <div class="m">在深圳市来福儿网络科技有限公司（广州分部）担任前端工程师，负责独立开发公司购物官网（移动端/PC端）前端页面，与美工设计人员沟通设计页面，与后台对接。学习 React.JS 框架，学习微信小程序。</div>
          </div>
          <div class="trapezoid">
            <div class="white"></div>
            <img src="../assets/company2.png">
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'slide4',
  props: ['message'],
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.inner{
  width: 1000px;
  margin: 0 auto;
  height: 100%;
  position: relative;
}
.now{
  width: 85px;
  height: 85px;
  border: 5px solid #98F5FF;
  border-radius: 50%;
  color: #074490;
  font: 26px/85px "Microsoft YaHei";
  font-weight: bold;
  position: absolute;
  left: 197px;
  top: 65px;
}
.line{
  width: 8px;
  height: 400px;
  background: #98F5FF;
  position: absolute;
  left: 245px;
  margin-left: -4px;
  top: 155px;
  border-radius: 4px;
}
.box-a,.box-b{
  width: 14px;
  height: 14px;
  background: #FFF;
  border-radius: 50%;
  position: absolute;
  left: 245px;
  margin-left: -9px;
  cursor: pointer;
  border: 2px solid #0744D0;
}
.box-a{
  top: 260px;
}
.box-b{
  top: 430px;
}
.line1{
  height: 1px;
  width: 20px;
  background: #0744D0;
  position: absolute;
  top: 7px;
  left: -20px;
}
.time{
  width: 100px;
  height: 32px;
  font-size: 16px/16px "Microsoft YaHei";
  position: absolute;
  top: -12px;
  left: -110px;
  color: #FFF;
}
.right-div{
  width: 650px;
  height: 150px;
  background: #EEE;
  position: absolute;
  left: 1000px;
  top: -70px;
  border-radius: 15px;
  animation: right-anim 0.7s;
  animation-fill-mode: forwards;
  box-shadow: 5px 5px 5px #888888;
}
.rightanim-enter-active {
  animation: right-anim 0.7s;
  animation-fill-mode: forwards;
}
.rightanim1-enter-active {
  animation: right-anim 0.7s;
  animation-fill-mode: forwards;
  animation-delay: 0.3s;
}
@keyframes right-anim {
  0% {
    left: 1000px;
  }
  100% {
    left: 50px;
  }
}
.right-div .tri{
  width: 0;
  height: 0;
  border-right: 20px solid #EEE;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  position: absolute;
  left: -20px;
  top: 55px;
}
.right-div .content{
  width: 480px;
  height: 120px;
  position: absolute;
  left: 10px;
  top: 5px;
}
.right-div .content .company{
  height: 25px;
  font: 23px/25px "Microsoft YaHei";
  position: absolute;
  left: 10px;
  top: 10px;
  color: #999;
}
.right-div .content .intro{
  height: 25px;
  font: 16px/25px "Microsoft YaHei";
  position: absolute;
  left: 10px;
  top: 40px;
  color: #999;
}
.right-div .content .m{
  width: 400px;
  font: 12px/17px "Microsoft YaHei";
  position: absolute;
  left: 10px;
  top: 70px;
  color: #333;
  text-align: left;
}

.box-a:hover .company,.box-a:hover .intro,.box-b:hover .company,.box-b:hover .intro{
  color: #074490;
}

.right-div .trapezoid{
  width: 150px;
  height: 0;
  border-right: 0px solid transparent;
  border-left: 50px solid transparent;
  border-top: 150px solid #FFF;
  position: absolute;
  right: 20px;
  top: 0px;
}
.right-div .trapezoid .white{
  width: 20px;
  height: 150px;
  background: #FFF;
  position: absolute;
  right: -20px;
  top: -150px;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 15px;
}
.box-a .trapezoid img{
  width: 170px;
  position: absolute;
  left: -5px;
  top: -115px;
}
.box-b .trapezoid img{
  width: 130px;
  position: absolute;
  left: 20px;
  top: -130px;
}
</style>



