<template>
  <div class="inner">
    <ul>
      <li v-for="list in lists" v-bind:class="list.classname">
        <div class="name">{{ list.name }}</div>
        <div class="gray"></div>
        <div class="blue-div">
          <transition name="blueanim">
            <div class="blue" v-if="message == 2"></div>
          </transition>
        </div>
      </li>
    </ul>
    <div class="skill-intro">
    精通HTML(5)/CSS(3)页面布局，高度还原设计稿<br/>
    高兼容性代码，兼容IE7+等各大主流浏览器<br/>
    原生JS编程，熟悉JS设计模式，熟练使用Ajax<br/>
    精通JQuery库，熟悉vue.js和react.js框架<br/>
    熟悉PS，对微信小程序有初步了解，熟悉github</div>
  </div>
</template>

<script>
export default {
  name: 'slide3',
  props: ['message'],
  data () {
    return {
      lists: [
        { name: 'HTML(5)/CSS(3)：', classname:'li1'},
        { name: 'Java：', classname:'li2'},
        { name: 'JavaScript/JQuery：', classname:'li3'},
        { name: 'Vue.js/React.js：', classname:'li4'},
        { name: 'photoshop：', classname:'li5'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.inner{
  width: 1000px;
  margin: 0 auto;
  height: 100%;
  position: relative;
}
ul{
  list-style-type: none;
  width: 700px;
  height: 20px;
  position: absolute;
  left: 50%;
  margin-left: -400px;
  top: 50px;
}
ul li{
  list-style-type: none;
  width: 700px;
  height: 20px;
  margin-bottom: 30px;
  position: relative;
}
ul li .name{
  width: 200px;
  height: 20px;
  font: 16px/20px "Microsoft YaHei";
  float: left;
  text-align: right;
  color: #074480;
  color: #FFF;
}
ul li .gray{
  width: 490px;
  height: 15px;
  background: #DDD;
  position: absolute;
  right: 0px;
  top: 4px;
  border-radius: 10px;
}
ul li .blue-div{
  height: 15px;
  position: absolute;
  left: 210px;
  top: 4px;
  border-radius: 10px;
}
ul li.li1 .blue-div{
  width: 400px;
}
ul li.li2 .blue-div{
  width: 300px;
}
ul li.li3 .blue-div{
  width: 400px;
}
ul li.li4 .blue-div{
  width: 200px;
}
ul li.li5 .blue-div{
  width: 200px;
}
ul li .blue{
  width: 100%;
  height: 15px;
  background: #074480;
  position: absolute;
  left: 0px;
  top: 0px;
  border-radius: 10px;
}
.blueanim-enter-active {
  animation: blue-anim 1s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}
@keyframes blue-anim {
  0% {
    width: 0;
  }
  80% {
    width: 102%;
  }
  100% {
    width: 100%;
  }
}

.skill-intro{
  width: 800px;
  height: 200px;
  position: absolute;
  left: 50%;
  margin-left: -400px;
  top: 340px;
  text-align: center;
  font: 15px/32px "Microsoft YaHei";
  color: #EEE;
}

</style>



