  margin-left: -100px;
  top: 80px;
  border-radius: 50%;
  opacity: 0;
}
.name{
  width: 200px;
  height: 34px;
  font: 34px/34px "Microsoft YaHei";
  position: absolute;
  left: 50%;
  margin-left: -100px;
  top: 350px;
