
<template>
  <div id="app">
      <swiper :options="swiperOption" ref="mySwiper" class="swiper">
        <swiper-slide class="slide1">
          <slide1 :message="pi1()"/>
        </swiper-slide>
        <swiper-slide class="slide2">
          <slide2 :message="pi1()"/>
       </swiper-slide>
        <swiper-slide class="slide3">
          <slide3 :message="pi1()"/>
        </swiper-slide>
        <swiper-slide class="slide4">
          <slide4 :message="pi1()"/>
        </swiper-slide>
        <swiper-slide class="slide5">
          <slide5 :message="pi1()"/>
        </swiper-slide>
        <!-- <swiper-slide class="slide6">
          <slide6 :message="pi1()"/>
        </swiper-slide> -->
        <div class="swiper-button-next" slot="button-next" v-bind:class="{ hide: nexthide()}">
        </div>
      </swiper>
      <pagination :message="pi1()" v-on:receive="recieveindex"/>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import slide1 from './components/slide1'
import slide2 from './components/slide2'
import slide3 from './components/slide3'
import slide4 from './components/slide4'
import slide5 from './components/slide5'
import slide6 from './components/slide5'
import pagination from './components/pagination'
export default {
  name: "app",
  components: {
    swiper,
    swiperSlide,
    slide1,
    slide2,
    slide3,
    slide4,
    slide5,
    slide6,
    pagination
  },
  data() {
      return {
        swiperOption: {
          
          paginationindex: 0,
          autoplay: false,
          speed:200,
          onlyExternal:true,
          direction : 'vertical',
          grabCursor : false,
          prevButton:'.swiper-button-prev',//上一张
          nextButton:'.swiper-button-next',//下一张
          mousewheelControl : true,
          observeParents:true,
          resistanceRatio : 0.5,
          onTransitionStart: function(swiper){
            //this.paginationindex = swiper.activeIndex;
            this.paginationindex = swiper.activeIndex;
          }/*,
          onTransitionEnd: function(swiper){
            this.paginationindex = swiper.activeIndex;
          }*/
        }
      }
    },
    methods: {
      pi1: function () {
        return this.swiperOption.paginationindex; 
      },
      nexthide: function () {
        return this.swiperOption.paginationindex == 4; 
      },
      recieveindex: function (text) {
        //Toast('监听到子组件变化'+text);
        //alert(this.swiperOption.paginationindex);
        this.swiperOption.paginationindex = text;
        this.swiper.slideTo(this.swiperOption.paginationindex, 200,true);
      }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    }


}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width: 100%;
  height: 100%;
  position: relative;
}
.inner{
  width: 1000px;
  margin: 0 auto;
  height: 100%;
  position: relative;
}
.swiper{
  width: 100%;
  height: 100%;
  position: relative;
}
.slide1,.slide3,.slide5{
  background: linear-gradient(to top right, #00B38A , #AAA);
  position: relative;
}
.slide2,.slide4,.slide6{
  background: linear-gradient(to bottom right, #00B38A , #AAA);
  position: relative;
}
.swiper-button-next{
  width: 50px;
  height: 50px;
  cursor: pointer;
  position: absolute;
  left: 50%;
  margin-left: -25px;
  top: 520px;
  background: url(./assets/down.png);
  background-size: 100% 100%;
  animation-name: next-anim;
  animation-duration:0.7s;
  animation-iteration-count:infinite;
}
.hide{
  display: none;
}
@keyframes next-anim{
0%   {top:520px;}
100% {top:550px;}
}
</style>



